function history(index: number, isEnd: boolean) {
  console.log(isEnd);
  const animation = !isEnd
    ? `fadeInOut ${interval}s ease-in-out ${interval * index}s forwards`
    : "fadeIn 2.5s ease-in-out forwards";
  console.log(animation);
  const chunk: History[] = data[index];
  return (
    <div style={{ animation }}>
      <Timeline
        theme={{
          root: {
            direction: {
              vertical: ``,
            },
          },
        }}
      >
        {chunk.map((item: History, itemIndex: number) => {
          return (
            <Timeline.Item
              key={`history-item-${index}-${itemIndex}`}
              theme={{
                root: {
                  vertical: "mb-5",
                },
              }}
            >
              {/* <Timeline.Point /> */}
              <Timeline.Content>
                <Timeline.Time
                  theme={{
                    time: "mb-1 text-xs font-normal leading-none text-black tracking-widest",
                  }}
                >
                  {item.date}
                </Timeline.Time>
                <Timeline.Title
                  theme={{
                    title:
                      "mb-1 text-sm font-semibold text-[#c2af67] tracking-widest ",
                  }}
                >
                  {item.title}
                </Timeline.Title>
                <Timeline.Body
                  theme={{
                    body: "mb-4 text-xs font-normal text-gray-500 tracking-widest",
                  }}
                >
                  <div>
                    <p>{item.description}</p>
                    {item?.imageUrl && (
                      <div className="w-full py-2 text-center">
                        <img
                          className="w-full h-80 object-contain"
                          src={item.imageUrl}
                        />
                      </div>
                    )}
                  </div>
                </Timeline.Body>
              </Timeline.Content>
            </Timeline.Item>
          );
        })}
      </Timeline>
    </div>
  );
}
